// –ó–¥–µ—Å—å —Å–ø–µ—Ü–∏—Ñ–∏—á–µ—Å–∫–∞—è –ª–æ–≥–∏–∫–∞, –∫–æ—Ç–æ—Ä—É—é –º—ã –Ω–µ –º–æ–∂–µ–º –≤—ã–Ω–µ—Å—Ç–∏ –≤ field
import {selectorsData} from '../orderForm'

  export default function field(form, conditions) {
    const number = form.querySelector(selectorsData.number);
    const code = form.querySelector(selectorsData.code);
    const country = form.querySelector(selectorsData.country);
  
    // –æ–±—â–∏–µ –≤ field.js –∏ —Å–ø–µ—Ü–∏—Ñ–∏—á–µ—Å–∫–∏–µ –ø—Ä–∞–≤–∏–ª–∞ –º–æ–∂–Ω–æ –ø—Ä–æ–∫–∏–¥—ã–≤–∞—Ç—å –≤ field
    function validate(field) {
      // required
      if (!field.value.trim()) {
        return "empty";
      }
      // => –æ–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è —Å–ø–µ—Ü–∏—Ñ–∏—á–µ—Å–∫–∏–º –ø—Ä–∞–≤–∏–ª–æ–º
      const match = field.value.match(/[0-9\s]+/);
      if (!match) {
        return "incorrect character";
      }
      if (match[0] === field.value) {
        return false;
      }
      return "incorrect character";
    }
  
    // => field.js
    let numberError = validate(number);
    let codeError = validate(code);
    let error = numberError || codeError;
    let touched = false;
    let value = {
      number: number.value,
      code: code.value
    };
  
    // –ø—Ä–æ—Å—Ç–µ–π—à–∏–π PubSub –ø–∞—Ç—Ç–µ—Ä–Ω, –º–æ–∂–Ω–æ —É–Ω–µ—Å—Ç–∏ –≤ field.js
    let subscribers = [];
    function subscribe(callback) {
      subscribers.push(callback);
      return () => {
        subscribers = subscribers.filter(item => item !== callback);
      };
    }
    function notify() {
      subscribers.forEach(callback => {
        callback({
          error,
          touched,
          value,
          fields: { number, code, country }
        });
      });
    }
  
    // —Å–æ—Ö—Ä–∞–Ω—è–µ–º –∏–∑–º–µ–Ω–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    number.addEventListener("keyup", () => {
      touched = true;
      numberError = validate(number);
      error = numberError || codeError;
      value.number = number.value;
      notify();
    });
    code.addEventListener("keyup", () => {
      touched = true;
      codeError = validate(code);
      error = numberError || codeError;
      value.code = code.value;
      notify();
    });
  
    return {
      subscribe,
      error,
      touched,
      value,
      validate: () => {
        numberError = validate(number);
        codeError = validate(code);
        error = numberError || codeError;
        return {
          error,
          touched,
          value,
          fields: { number, code, country }
        };
      },
      prepareToSubmit: () => {
        touched = true;
      },
      fields: { number, code, country }
    };
  }